/**
 * Common utilities for TypeScript hooks
 * Ported from hooks/_lib/common.sh
 */

import { existsSync, statSync, renameSync, mkdirSync, readSync } from 'node:fs';
import { bufferWrite } from './analytics-buffer.js';
import { execSync } from 'node:child_process';
import type { HookResult, HookInput } from '../types.js';
import {
  getLogDir as getLogDirFromPaths,
  getProjectDir as getProjectDirFromPaths,
  getPluginRoot as getPluginRootFromPaths,
} from './paths.js';
import { getOrGenerateSessionId } from './session-id-generator.js';

// -----------------------------------------------------------------------------
// Environment and Paths
// All functions read env vars dynamically to support testing
// Re-export from paths.ts for cross-platform compatibility
// -----------------------------------------------------------------------------

/**
 * Get the log directory path (cross-platform)
 * Delegates to paths.ts for correct path handling on all platforms
 */
export function getLogDir(): string {
  return getLogDirFromPaths();
}

/**
 * Get the project directory (cross-platform)
 * Delegates to paths.ts for correct path handling on all platforms
 */
export function getProjectDir(): string {
  return getProjectDirFromPaths();
}

/**
 * Get the plugin root directory (cross-platform)
 * Delegates to paths.ts for correct path handling on all platforms
 */
export function getPluginRoot(): string {
  return getPluginRootFromPaths();
}

/**
 * Get the environment file path (CC 2.1.25: CLAUDE_ENV_FILE support)
 * Falls back to .instance_env for backward compatibility
 */
export function getEnvFile(): string {
  if (process.env.CLAUDE_ENV_FILE) {
    return process.env.CLAUDE_ENV_FILE;
  }
  // Fallback to legacy .instance_env
  const pluginRoot = getPluginRoot();
  return `${pluginRoot}/.claude/.instance_env`;
}

/**
 * Get the session ID
 *
 * Resolution order:
 * 1. CLAUDE_SESSION_ID env var (from CC runtime - preferred)
 * 2. Cached session ID (from .instance/session-id.json)
 * 3. Generate smart session ID: {project}-{branch}-{MMDD}-{HHMM}-{hash4}
 *
 * Example smart ID: "orchestkit-main-0130-1745-a3f2"
 *
 * The old fallback format "fallback-{pid}-{timestamp}" was confusing and unhelpful.
 * Smart IDs are human-readable, showing project, branch, and time context.
 */
export function getSessionId(): string {
  return getOrGenerateSessionId();
}

/**
 * Get cached git branch (set at session start or first call)
 * Caches result in process.env to avoid repeated execSync calls
 */
export function getCachedBranch(projectDir?: string): string {
  if (process.env.ORCHESTKIT_BRANCH) {
    return process.env.ORCHESTKIT_BRANCH;
  }

  try {
    const branch = execSync('git branch --show-current', {
      cwd: projectDir || getProjectDir(),
      encoding: 'utf8',
      timeout: 5000,
      stdio: ['pipe', 'pipe', 'pipe'],
    }).trim();
    process.env.ORCHESTKIT_BRANCH = branch;
    return branch;
  } catch {
    return 'unknown';
  }
}

/**
 * Get log level (debug|info|warn|error, default: warn)
 */
export function getLogLevel(): string {
  return process.env.ORCHESTKIT_LOG_LEVEL || 'warn';
}

/**
 * Normalize line endings from CRLF to LF for cross-platform compatibility.
 * Windows uses \r\n (CRLF) while Unix uses \n (LF).
 * This is critical for parsing YAML frontmatter where we match '---' exactly.
 */
export function normalizeLineEndings(content: string): string {
  return content.replace(/\r\n/g, '\n');
}

/**
 * Check if should log at given level
 */
export function shouldLog(level: 'debug' | 'info' | 'warn' | 'error'): boolean {
  const levels = ['debug', 'info', 'warn', 'error'];
  return levels.indexOf(level) >= levels.indexOf(getLogLevel());
}

// -----------------------------------------------------------------------------
// Output Helpers (CC 2.1.7+ compliant)
// -----------------------------------------------------------------------------

/**
 * Output silent success - hook completed without errors, no user-visible output
 */
export function outputSilentSuccess(): HookResult {
  return { continue: true, suppressOutput: true };
}

/**
 * Output silent allow - permission hook approves silently
 */
export function outputSilentAllow(): HookResult {
  return {
    continue: true,
    suppressOutput: true,
    hookSpecificOutput: { permissionDecision: 'allow' },
  };
}

/**
 * Output block - stops the operation with an error
 */
export function outputBlock(reason: string): HookResult {
  return {
    continue: false,
    stopReason: reason,
    hookSpecificOutput: {
      permissionDecision: 'deny',
      permissionDecisionReason: reason,
    },
  };
}

/**
 * Output with additionalContext - injects context before tool execution (CC 2.1.9)
 * For PostToolUse hooks (hookEventName optional)
 */
export function outputWithContext(ctx: string): HookResult {
  return {
    continue: true,
    suppressOutput: true,
    hookSpecificOutput: {
      hookEventName: 'PostToolUse',
      additionalContext: ctx,
    },
  };
}

/**
 * Output with additionalContext for UserPromptSubmit hooks (CC 2.1.9)
 * hookEventName is REQUIRED for UserPromptSubmit
 */
export function outputPromptContext(ctx: string): HookResult {
  return {
    continue: true,
    suppressOutput: true,
    hookSpecificOutput: {
      hookEventName: 'UserPromptSubmit',
      additionalContext: ctx,
    },
  };
}

/**
 * Output with user notification + Claude context (CC 2.1.9+)
 * Issue #278: Dual-channel output for three-tier UX
 *
 * @param userMessage - Brief message shown to user via systemMessage (optional)
 * @param claudeContext - Full context for Claude via additionalContext (optional)
 * @returns HookResult with appropriate channels set
 *
 * Usage:
 * - Both set: User sees notification, Claude gets context
 * - Only claudeContext: Silent injection (Claude-only)
 * - Only userMessage: User notification without context
 */
export function outputWithNotification(
  userMessage: string | undefined,
  claudeContext: string | undefined
): HookResult {
  const result: HookResult = {
    continue: true,
    suppressOutput: true,
  };

  if (userMessage) {
    result.systemMessage = userMessage;
  }

  if (claudeContext) {
    result.hookSpecificOutput = {
      hookEventName: 'UserPromptSubmit',
      additionalContext: claudeContext,
    };
  }

  return result;
}

/**
 * Output allow with additionalContext - permission hook approves with context (CC 2.1.9)
 */
export function outputAllowWithContext(ctx: string, systemMessage?: string): HookResult {
  const result: HookResult = {
    continue: true,
    hookSpecificOutput: {
      hookEventName: 'PreToolUse',
      additionalContext: ctx,
      permissionDecision: 'allow',
    },
  };

  if (systemMessage) {
    result.systemMessage = systemMessage;
  } else {
    result.suppressOutput = true;
  }

  return result;
}

/**
 * Output error message - only use when there's an actual problem
 */
export function outputError(message: string): HookResult {
  return { continue: true, systemMessage: message };
}

/**
 * Output warning message via JSON stdout — visible to both user and Claude (CC 2.1.7+).
 * Use this when Claude needs to see and act on the warning (e.g., cost advice, quality gates).
 * For user-only warnings where Claude should NOT see the message, use outputStderrWarning.
 */
export function outputWarning(message: string): HookResult {
  return { continue: true, systemMessage: `\u26a0 ${message}` };
}

/**
 * Output warning via stderr + exit(2) — visible to user only, Claude does NOT see it (CC 2.1.39).
 * Use this for informational warnings that should not influence Claude's behavior
 * (e.g., deprecation notices, non-actionable advisories).
 *
 * IMPORTANT: This function calls process.exit(2) and never returns.
 * Do NOT use inside unified dispatchers — it will crash the dispatcher process.
 * Only use in standalone hook entry points.
 */
export function outputStderrWarning(message: string): never {
  process.stderr.write(`\u26a0 ${message}\n`);
  process.exit(2);
}

/**
 * Output deny with feedback logging (CC 2.1.7)
 */
export function outputDeny(reason: string): HookResult {
  return {
    continue: false,
    stopReason: reason,
    hookSpecificOutput: {
      hookEventName: 'PreToolUse',
      permissionDecision: 'deny',
      permissionDecisionReason: reason,
    },
  };
}

/**
 * Output with updatedInput - modifies tool input before execution (CC 2.1.25)
 * Canonical way to modify tool inputs from PreToolUse hooks
 */
export function outputWithUpdatedInput(updatedInput: Record<string, unknown>): HookResult {
  return {
    continue: true,
    suppressOutput: true,
    hookSpecificOutput: {
      hookEventName: 'PreToolUse',
      updatedInput,
    },
  };
}

// -----------------------------------------------------------------------------
// Logging (with log level guard for performance)
// -----------------------------------------------------------------------------

const LOG_ROTATION_MAX_SIZE = 200 * 1024; // 200KB
const PERMISSION_LOG_MAX_SIZE = 100 * 1024; // 100KB

/**
 * Rotate log file if it exceeds size limit
 */
function rotateLogFile(logFile: string, maxSize: number): void {
  if (!existsSync(logFile)) return;

  try {
    const stats = statSync(logFile);
    if (stats.size > maxSize) {
      const rotated = `${logFile}.old.${Date.now()}`;
      renameSync(logFile, rotated);
    }
  } catch {
    // Ignore rotation errors
  }
}

/**
 * Ensure directory exists
 */
function ensureDir(dir: string): void {
  if (!existsSync(dir)) {
    mkdirSync(dir, { recursive: true });
  }
}

/**
 * Log to hook log file with automatic rotation
 * Respects ORCHESTKIT_LOG_LEVEL (default: warn, skips debug logs in production)
 */
export function logHook(hookName: string, message: string, level: 'debug' | 'info' | 'warn' | 'error' = 'debug'): void {
  // Skip if below log level threshold (big perf win - avoids I/O)
  if (!shouldLog(level)) {
    return;
  }

  const logDir = getLogDir();
  const logFile = `${logDir}/hooks.log`;

  try {
    ensureDir(logDir);
    rotateLogFile(logFile, LOG_ROTATION_MAX_SIZE);

    const timestamp = new Date().toISOString().replace('T', ' ').slice(0, 19);
    bufferWrite(logFile, `[${timestamp}] [${level.toUpperCase()}] [${hookName}] ${message}\n`);
  } catch {
    // Ignore logging errors - don't block hook execution
  }
}

/**
 * Log permission decision for audit trail (CC 2.1.7 feature)
 * Always logs (security audit trail) - not affected by log level
 */
export function logPermissionFeedback(
  decision: 'allow' | 'deny' | 'warn',
  reason: string,
  input?: HookInput | Record<string, unknown>
): void {
  const logDir = getLogDir();
  const logFile = `${logDir}/permission-feedback.log`;

  try {
    ensureDir(logDir);
    rotateLogFile(logFile, PERMISSION_LOG_MAX_SIZE);

    const timestamp = new Date().toISOString();
    const toolName = (input as HookInput)?.tool_name || process.env.HOOK_TOOL_NAME || 'unknown';
    const sessionId = (input as HookInput)?.session_id || getSessionId();

    bufferWrite(
      logFile,
      `${timestamp} | ${decision} | ${reason} | tool=${toolName} | session=${sessionId}\n`
    );
  } catch {
    // Ignore logging errors
  }
}

// -----------------------------------------------------------------------------
// Token Estimation
// -----------------------------------------------------------------------------

/**
 * Content-aware token estimation (~80% accuracy without external tokenizer).
 * Code-heavy content averages ~2.8 chars/token; prose ~3.5 chars/token.
 */
export function estimateTokenCount(content: string): number {
  if (!content) return 0;
  const codeIndicators = (content.match(/[{};()=><]/g) || []).length;
  const codeRatio = codeIndicators / content.length;
  const charsPerToken = codeRatio > 0.03 ? 2.8 : 3.5;
  return Math.ceil(content.length / charsPerToken);
}

// -----------------------------------------------------------------------------
// Budgeted Output Helpers
// -----------------------------------------------------------------------------

/**
 * Output prompt context with token budget awareness.
 * Checks if the category is over budget before injecting.
 * Falls back to silent success when budget exhausted.
 *
 * Accepts budget checker and tracker as parameters to avoid circular deps.
 * If not provided, falls back to unchecked injection.
 */
export function outputPromptContextBudgeted(
  ctx: string,
  hookName: string,
  category: string,
  budgetChecker?: { isOverBudget: (cat: string) => boolean },
  tokenTracker?: { trackTokenUsage: (hook: string, cat: string, tokens: number) => void },
): HookResult {
  const tokens = estimateTokenCount(ctx);

  if (budgetChecker?.isOverBudget(category)) {
    logHook(hookName, `Budget exhausted for ${category}, suppressing ${tokens}t`);
    return outputSilentSuccess();
  }

  if (tokenTracker) {
    tokenTracker.trackTokenUsage(hookName, category, tokens);
  }

  return outputPromptContext(ctx);
}

// -----------------------------------------------------------------------------
// Input Helpers
// -----------------------------------------------------------------------------

/**
 * Read hook input from stdin synchronously
 * Returns parsed JSON or empty object on failure
 */
export function readHookInput(): HookInput {
  try {
    // Read from stdin synchronously
    const chunks: Buffer[] = [];
    const BUFSIZE = 256;
    const buf = Buffer.allocUnsafe(BUFSIZE);

    let bytesRead: number;
    const fd = 0; // stdin

    while (true) {
      try {
        bytesRead = readSync(fd, buf, 0, BUFSIZE, null);
        if (bytesRead === 0) break;
        chunks.push(Buffer.from(buf.subarray(0, bytesRead)));
      } catch {
        break;
      }
    }

    const input = Buffer.concat(chunks).toString('utf8').trim();
    if (!input) {
      return { tool_name: '', session_id: getSessionId(), tool_input: {} };
    }

    return JSON.parse(input);
  } catch {
    return { tool_name: '', session_id: getSessionId(), tool_input: {} };
  }
}

/**
 * Get field from hook input using optional chaining
 */
export function getField<T>(input: HookInput, path: string): T | undefined {
  const parts = path.replace(/^\./, '').split('.');
  let value: unknown = input;

  for (const part of parts) {
    if (value === null || value === undefined) return undefined;
    value = (value as Record<string, unknown>)[part];
  }

  return value as T;
}

// -----------------------------------------------------------------------------
// ReDoS-Safe String Matching
// -----------------------------------------------------------------------------

/**
 * Check if any single line contains all specified substrings (ReDoS-safe O(n)).
 * Use instead of polynomial regexes like /A.*B.*C/.test(content).
 */
export function lineContainsAll(content: string, ...terms: string[]): boolean {
  return content.split('\n').some(line => terms.every(t => line.includes(t)));
}

/**
 * Check if any single line contains all specified substrings (case-insensitive, ReDoS-safe O(n)).
 */
export function lineContainsAllCI(content: string, ...terms: string[]): boolean {
  return content.split('\n').some(line => {
    const lower = line.toLowerCase();
    return terms.every(t => lower.includes(t.toLowerCase()));
  });
}

// -----------------------------------------------------------------------------
// String Utilities
// -----------------------------------------------------------------------------

/**
 * Normalize command: remove line continuations and collapse whitespace
 * Prevents bypassing detection with backslash-newline tricks (CC 2.1.6 fix)
 */
export function normalizeCommand(command: string): string {
  return command
    .replace(/\\\s*[\r\n]+/g, ' ') // Remove line continuations
    .replace(/\n/g, ' ') // Replace newlines with spaces
    .replace(/\s+/g, ' ') // Collapse whitespace
    .trim();
}

/**
 * Escape string for use in regex
 */
export function escapeRegex(str: string): string {
  return str.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
}
